import * as React from 'react';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import StepContent from '@mui/material/StepContent';
import Button from '@mui/material/Button';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import { FormControlLabel, MobileStepper, Stack, Switch, TextField, useTheme } from '@mui/material';
import { KeyboardArrowLeft, KeyboardArrowRight } from '@mui/icons-material';

const steps = [
  {
    label: 'Player count'
  },
  {
    label: 'Player Names'
  },
  {
    label: 'Game Settings'
  },
];


function getStepContent(step: number) {
  switch (step) {
      case 0:
          return (
              <form >
                <TextField
                  required
                  id="numberofPlayers"
                  label="Number of players"
                  type="number"
                  variant="outlined"
                  sx={{ mt: 2, mr: 1, mb: 2}}
                  inputProps={{ inputMode: 'numeric' }}
                />
              </form>
          );
      case 1:
          return (
            <form >
              <TextField
                  required
                  id="p1"
                  label="Player 1"
                  type="text"
                  variant="outlined"
                  sx={{ mt: 2, mr: 1, mb: 2}}
                  inputProps={{ inputMode: 'text' }}
                />
              <TextField
                  required
                  id="p1"
                  label="Player 2"
                  type="text"
                  variant="outlined"
                  sx={{ mt: 2, mr: 1, mb: 2}}
                  inputProps={{ inputMode: 'text' }}
                />
                <TextField
                  required
                  id="p1"
                  label="Player 3"
                  type="text"
                  variant="outlined"
                  sx={{ mt: 2, mr: 1, mb: 2}}
                  inputProps={{ inputMode: 'text' }}
                />
                
            
            </form>     
            
          );
      case 2:
          return (
              <form>
                <FormControlLabel sx={{ mt: 1, mr: 1, mb: 1}} control={<Switch disabled defaultChecked />} label="Yassat Classic" />
              </form>
          );
      default:
          return 'Unknown step';
  }
}

export default function VerticalLinearStepper() {
  const [activeStep, setActiveStep] = React.useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };
                  
  return (
    <Stack
    direction="column"
    alignItems="center"
    sx={{
      height: '100vh',
      display: 'flex',
      bgcolor: 'background.default',
      color: 'text.primary',
      mt: 2
    }}
  >
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((step, index) => (
          <Step key={step.label}>
            <StepLabel
              // optional={
              //   index === 2 ? (
              //     <Typography variant="caption">Last step</Typography>
              //   ) : null
              // }
            >
              {step.label}
            </StepLabel>
            <StepContent>
              <Typography>{getStepContent(index)}</Typography>
              <Box sx={{ mb: 2 }}>
                <div>

                  <Button
                    disabled={index === 0}
                    onClick={handleBack}
                    sx={{ mt: 1, mr: 1 }}
                  >
                    Back
                  </Button>
                  <Button
                    variant="contained"
                    onClick={handleNext}
                    sx={{ mt: 1, mr: 1 }}
                  >
                    {index === steps.length - 1 ? 'Finish' : 'Continue'}
                  </Button>
                </div>
              </Box>
            </StepContent>
          </Step>
        ))}
      </Stepper>
      {activeStep === steps.length && (
        <Paper square elevation={1} sx={{ p: 3, mt: 2 }}>
          <Typography>All steps completed - Ready?</Typography>
          <Button onClick={handleReset} sx={{ mt: 5, mr: 1 }}>
            reset
          </Button>
          <Button variant="contained" color="success" sx={{ mt: 5 }}>
            Start Game
          </Button>
        </Paper>
      )}
    </Stack>
  );
}